<template>
  <div id="app">
    <china-map :key="key" cascade :echartsData="echartsData" :scatterData="scatterData" @mapClick="mapClick" />
    <audio id="background-music" :src="require('@/assets/music/music.m4a')" loop autoplay></audio>
  </div>
</template>

<script>
import chinaMap from "./components/chinaMap.vue";

export default {
  name: "App",
  components: {
    chinaMap,
  },
  data() {
    return {
      key: 0,
      testData: [
        {
          name: "天津市",
          value: 1,
          tag: 0,
        },
        {
          name: "陕西省",
          value: 1,
          tag: 0,
        },
        {
          name: "西安市",
          value: 1,
          tag: 1,
        },
        {
          name: "四川省",
          value: 1,
          tag: 0,
        },
        {
          name: "成都市",
          value: 1,
          tag: 1,
        },
        {
          name: "眉山市",
          value: 1,
          tag: 1,
        },
      ],
      scatterData: [],
    };
  },
  created() {/*用于染色地图块*/ 
    this.echartsData = this.testData.map(({ name, value, tag }) => {
      return {
        name: this.getCityName(name, tag),
        value
      }
    })
  },
  mounted() {

    const audio = document.getElementById('background-music');
    audio.volume = 0.5; // 设置音量
    audio.addEventListener('canplaythrough', () => {
      console.log('Audio can play through');
    });
    audio.addEventListener('play', () => {
      console.log('Audio is playing');
    });
    audio.addEventListener('error', (e) => {
      console.error('Audio error', e);
    });
    this.createMeteors();
  },
  methods: {
    mapClick({ code, name }) {
      console.log(code, name);
    },
    /**
     *@description 这个方法用来获取城市的简称，例如 北京市 => 北京。 内蒙古自治区 => 内蒙古
    */
    getCityName(cityName, tag) {
      if (!cityName) return;
      if (tag) {
        return cityName;
      }
      let isAutonomy = cityName.search("自治区") != -1;
      if (isAutonomy) {
        return cityName.search("内") != -1
          ? cityName.substr(0, 3)
          : cityName.substr(0, 2);
      } else {
        return cityName.match(/(\S*)(省|市|特别行政区)/)
          ? cityName.match(/(\S*)(省|市|特别行政区)/)[1]
          : cityName;
      }
      // console.log('list2', this.list)
    },
    createMeteors() {
      const meteorContainer = document.getElementById('meteors');
      const numberOfMeteors = 100; // 增加流星数量
      for (let i = 0; i < numberOfMeteors; i++) {
        const meteor = document.createElement('div');
        meteor.classList.add('meteor');

        // 随机位置和动画延迟
        const startPos = Math.random() * 100;
        const delay = Math.random() * 5;
        const duration = Math.random() * 3 + 2;

        // 随机选择从顶部或左侧出现
        if (Math.random() < 0.5) {
          meteor.style.top = '-' + Math.random() * 10 + 'vh';
          meteor.style.left = startPos + 'vw';
        } else {
          meteor.style.top = startPos + 'vh';
          meteor.style.left = '-' + Math.random() * 10 + 'vw';
        }

        meteor.style.animationDuration = duration + 's';
        meteor.style.animationDelay = delay + 's';
        meteorContainer.appendChild(meteor);

      }
    },
  }
};
</script>
<style>
#app {
  background-color: rgba(15, 30, 51, 0);
}

#meteors {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1;
  /* background-color: rgba(15, 30, 51, 1); */
  background-image: url('../img/bg/bg.jpeg');
  background-size: cover; /* 确保图片覆盖整个背景 */
  background-position: center; /* 将背景图片居中 */
  background-repeat: no-repeat; /* 禁止背景图片重复 */
}

.meteor {
  position: absolute;
  width: 1px;
  height: 80px;
  background: linear-gradient(45deg, rgb(249, 223, 254), rgba(242, 252, 191, 0.1));
  animation: fall linear infinite;
}

@keyframes fall {
  from {
    transform: translateY(-100px) translateX(0) rotate(315deg);
  }

  to {
    transform: translateY(110vh) translateX(100vw) rotate(315deg);
  }
}
</style>
